import Vue from 'vue'
import App from './App.vue'
import router from './router'
import Head from 'vue-head'
import Polyfill from '@babel/polyfill'

Vue.use(Head, {
  // デフォルトは'|'
  separator: '-',
  complement: 'ちょび・ツール',
});
Vue.use(Polyfill);

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
